import $http from "./http.js"
//
const common = {
    //  
    hotelInfo(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
}
//
const roomBooking = {
    //  
    activitylist(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //
    roomCategory(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //  
    specificActivity(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    }
}
//
const foodBooking = {
    //  
    foodlist(obj) {
        return $http.get({
            url: "/mobile/78CDF1/food/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    // 
    foodsettlement(obj) {
        return $http.post({
            url: "/mobile/78CDF1/food/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //
    foodOrderDetails(obj) {
        return $http.get({
            url: "/mobile/78CDF1/food/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    // 
    foodpay(obj) {
        return $http.post({
            url: "/mobile/78CDF1/food/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    }
}

export {
    common,
    roomBooking,
    foodBooking
}


